import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import Swal from 'sweetalert2';
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {ServiceEditComponent} from "../service-edit/service-edit.component";
import {AuthenticationService} from '../../../../auth/service';
import {Service} from '../service';
import {LimitsEditComponent} from '../limits-edit/limits-edit.component';
import {ServicesService} from "../services.service";
import {ServiceLimitsComponent} from "../service-limits/service-limits.component";
import {Router} from "@angular/router";

@Component({
    selector: 'app-service-header',
    templateUrl: './service-header.component.html',
    styleUrls: ['./service-header.component.scss']
})
export class ServiceHeaderComponent implements OnInit {
    constructor(private auth: AuthenticationService,
                private ser: ServicesService,
                private router:Router,
                private modal: NgbModal) {
    }

    @Output() clicked = new EventEmitter()
    @Output() canceled = new EventEmitter()
    @Input() subbed = true
    @Input() loading = false
    loadingSub
    @Input() info
    @Input() service: Service
    @Output() reload = new EventEmitter()
    copied = false
    loadingCancel = false


    ngOnInit() {
        this.auth.currentUser.subscribe(v => {
            if(!v) { return }
            this.subbed = v.customer_active_services?.includes(this.service.service_key)
        })
    }
    cancel() {
        console.log('canceling.. ')
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#7367F0',
            cancelButtonColor: '#E42728',
            confirmButtonText: 'Yes, Cancel my Subscription',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            }
        }).then((result) => {
            if (result.value) {
                this.loadingCancel=true
                this.ser.deactivate(this.service.service_key).then( res=>{
                this.auth.removeService(this.service.service_key)
                this.canceled.emit(true)
                Swal.fire({
                    icon: 'success',
                    title: 'Canceled!',
                    text: 'Your Subscription Has Been Canceled Successfully!',
                    customClass: {
                        confirmButton: 'btn btn-success'
                    }
                })})
            }
        }).catch(error=>{
            Swal.fire({
                    icon: 'error',
                    title: 'Oops',
                    text: 'Something went wrong'
                }
            ).finally(()=>{
                this.loadingCancel=false
            })
        });

    }

    edit() {
        const modal = this.modal.open(ServiceEditComponent)
        modal.componentInstance.service = this.service
        modal.componentInstance.keyChanged.subscribe(v=>{
            this.router.navigate(['/services/details/',v])
        })
        modal.result?.then((r) => {
            if (r === 'reload') {
                this.reload.emit()
            }
        })
    }

    manage() {
         let modal=this.modal.open(ServiceLimitsComponent, {
            size: 'lg'
        })
        modal.componentInstance.limits=this.service.limits
        modal.componentInstance.serviceKey=this.service.service_key
        modal.componentInstance.newLimits.subscribe(v=>{
            this.service.limits=v
            console.log('new limits: ',v)
        })
    }

    copy() {
        navigator.clipboard.writeText(this.service.service_main_token)
        this.copied = true
        setTimeout(() => {
            this.copied = false
        }, 2000)
    }

    sub(e) {
        this.clicked.emit(e)
    }

}
