import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {ServicesService} from '../../services.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {HttpErrorResponse} from '@angular/common/http';
import {Endpoint} from '../endpoint';

@Component({
  selector: 'app-endpoints-edit',
  templateUrl: './endpoints-edit.component.html',
  styleUrls: ['./endpoints-edit.component.scss']
})
export class EndpointsEditComponent implements OnInit {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private service: ServicesService
  ) {
  }

  @Input() serviceKey: string
  @Input() endpoint: Endpoint
  @Output() reload = new EventEmitter()
  submitted = false
  taken = false

  loading = false
  form = new FormGroup({
    "endpoint_route": new FormControl(null, [Validators.required]),
    "endpoint_payload_template":  new FormControl(null, [Validators.required]),
    "endpoint_payload_example":  new FormControl(null, [Validators.required]),
    "endpoint_response_example":  new FormControl(null, [Validators.required]),
    "endpoint_method": new FormControl(null),
  })

  ngOnInit() {
    const {endpoint_route, endpoint_response_example, endpoint_payload_template, endpoint_method, endpoint_payload_example} = this.endpoint
    this.form.setValue({endpoint_route, endpoint_method,
      endpoint_response_example: JSON.stringify(endpoint_response_example),
      endpoint_payload_template: JSON.stringify(endpoint_payload_template),
      endpoint_payload_example: JSON.stringify(endpoint_payload_example),
    })
  }

  save() {
    this.submitted = true
    this.taken = false
    if (!this.form) { return }
    const endpoint = this.form.value
    this.loading = true
    this.service.updateEndpoint(this.endpoint.endpoint_id, endpoint).then(res => {
      if (res?.error === 'exists') {
        this.taken = true
      }
      if (res?.success) {
        this.toastr.success('Endpoint updated successfully ')
        this.activeModal.close('reload')
      } else {
        this.toastr.error('Something went wrong')
      }
    }).catch((err: HttpErrorResponse) => {
      this.toastr.error('Something went wrong')
    }).finally(() => this.loading = false)
  }
}
