import {Injectable} from '@angular/core';
import {HttpClientService} from "../../../../@core/services/http-client.service";
import {Constants} from "../../../constants";
import {AuthenticationService} from "../../../auth/service";
import {Service} from './service';
import {Endpoint} from './endpoints/endpoint';
import {tryParse} from '../../utils/validation';
import {Limits} from "../tokens/token";

@Injectable({
    providedIn: 'root'
})
export class ServicesService {
    canAdd = false
    canEdit = false

    constructor(
        private http: HttpClientService,
        private auth: AuthenticationService
    ) {
        this.auth.currentUser.subscribe(v => {
            if (!v) {return}
            this.canAdd = v.permissions.includes("create-service")
            this.canEdit = v.permissions.includes("update-service")
        })
    }

    byKey(key: string) {
        return this.http.getData(Constants.API_GET_SERVICES + '/' + key)
    }
    getServices(page= 1, q: string= null) {
        return this.http.getData(Constants.API_GET_SERVICES, {q, page})
    }
    isSubbed(key:string){
        return this.auth.currentUserValue.customer_active_services?.includes(key)
    }

    getActivatedServices() {
        return this.http.getData(Constants.API_ACTIVATED_SERVICE)
    }
    deactivate(key: string) {
        return this.http.getData(Constants.API_DEACTIVATE + key + '/deactivate')
    }

    updateLimits(obj) {
        return this.http.putData(Constants.API_LIMITS, obj)
    }
    updateServiceLimits(key:string,o:Partial<Limits>){
        return this.http.putData(Constants.API_ACTIVE_SERVICES+'limit/'+key,o )
    }

    getLimits() {
        return this.http.getData(Constants.API_GET_CUSTOMER_LIMITS)
    }

    add(service: Partial<Service>) {
        // TODO remove after implementing dynamic headers
        if (service.service_header_template) {service.service_header_template = tryParse('' + service.service_header_template)}
        return this.http.postData(Constants.API_ADD_SERVICE, {...service, service_key: service.service_key?.toLowerCase()})
    }
    updateEndpoint(id: number, endpoint: Partial<Endpoint>) {
        if(typeof endpoint.endpoint_payload_template === 'string'){
            endpoint.endpoint_payload_template=tryParse(endpoint.endpoint_payload_template)
            console.warn('endpoint.endpoint_payload_template',endpoint.endpoint_payload_template)
        }
        endpoint.endpoint_payload_template = endpoint.endpoint_payload_template
            .map(p => ({key: p.key, validation: p.validation, value: p.value}))
        console.warn(tryParse(endpoint.endpoint_payload_template as any))
        return this.http.putData(Constants.API_ENDPOINTS + '/' + id,
            {...endpoint, endpoint_response_example: tryParse(endpoint.endpoint_response_example),
                endpoint_payload_template: tryParse(endpoint.endpoint_payload_template as any),
                endpoint_payload_example: tryParse(endpoint.endpoint_payload_example)
            })
    }

    addEndpoint(id: string, endpoint) {
        return this.http.postData(Constants.API_ENDPOINTS,
            {...endpoint, endpont_service_id: id, endpoint_response_example: tryParse(endpoint.endpoint_response_example),
                endpoint_payload_template: tryParse(endpoint.endpoint_payload_template),
                endpoint_payload_example: tryParse(endpoint.endpoint_payload_example)
            })
    }
    deleteEndpoint(id: number) {
        return this.http.deleteData(Constants.API_ENDPOINTS + '/' + id)
    }
    update(service: Partial<Service>, key: string) {
        // TODO remove after implementing dynamic headers
        if (service.service_header_template) {service.service_header_template = JSON.parse('' + service.service_header_template
            ).map(t => ({value: t.value, key: t.key, validation: t.validation})) }
        return this.http.putData(Constants.API_ADD_SERVICE + '/' + key, {...service, service_key: service.service_key?.toLowerCase()})
    }
    activate(key: string, token?: string) {
        let o: any = {service_key: key}
        if (token) {o.token = token}
        return this.http.postData(Constants.API_ACTIVATE_SERVICE , o)
    }
}




