import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ServicesListComponent } from './services-list/services-list.component';
import {RouterModule, Routes} from '@angular/router';
import {NgSelectModule} from '@ng-select/ng-select';
import {ComponentsModule} from "../../components/components.module";
import { ServiceFullComponent } from './service-full/service-full.component';
import { ServiceHeaderComponent } from './service-header/service-header.component';
import {BreadcrumbModule} from "../../../layout/components/content-header/breadcrumb/breadcrumb.module";
import {NgbNavModule, NgbPaginationModule, NgbProgressbarModule} from '@ng-bootstrap/ng-bootstrap';
import { ServiceRelatedComponent } from './service-related/service-related.component';
import { ServiceSubscribeModalComponent } from './service-subscribe-modal/service-subscribe-modal.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {SweetAlert2Module} from '@sweetalert2/ngx-sweetalert2';
import { ServiceAddComponent } from './service-add/service-add.component';
import {CoreCommonModule} from "../../../../@core/common.module";
import { ServiceEditComponent } from './service-edit/service-edit.component';
import {EndpointsModule} from './endpoints/endpoints.module';
import {TokensModule} from '../tokens/tokens.module';
import { LimitsEditComponent } from './limits-edit/limits-edit.component';
import { ServiceActivatedComponent } from './service-activated/service-activated.component';
import {CoreCardModule} from "../../../../@core/components/core-card/core-card.module";
import { ServiceLimitsComponent } from './service-limits/service-limits.component';
import {CorePipesModule} from "../../../../@core/pipes/pipes.module";


const appRoutes: Routes = [

    {
        component: ServiceActivatedComponent,
        path: 'activated',
        pathMatch: 'full'
    },
  {
    component: ServicesListComponent,
    path: 'list',
    pathMatch: 'full'
  },
    {
        component: ServiceFullComponent,
        path: 'details/:key',
    },
];


@NgModule({
  declarations: [
    ServicesListComponent,
    ServiceFullComponent,
    ServiceHeaderComponent,
    ServiceRelatedComponent,
    ServiceSubscribeModalComponent,
    ServiceAddComponent,
    ServiceEditComponent,
    LimitsEditComponent,
    ServiceActivatedComponent,
    ServiceLimitsComponent,
  ],
    imports: [
        RouterModule.forChild(appRoutes),
        CommonModule,
        NgSelectModule,
        ComponentsModule,
        BreadcrumbModule,
        NgbNavModule,
        FormsModule,
        ReactiveFormsModule,
        NgbProgressbarModule,
        SweetAlert2Module,
        NgbPaginationModule,
        CoreCommonModule,
        EndpointsModule,
        TokensModule,
        CoreCardModule,
    ]
})
export class ServicesModule { }
