import {Component, OnInit} from '@angular/core';
import {Service} from '../service';
import {ServicesService} from '../services.service';
import {ToastrService} from 'ngx-toastr';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {ServiceAddComponent} from '../service-add/service-add.component';

@Component({
  selector: 'app-service-activated',
  templateUrl: './service-activated.component.html',
  styleUrls: ['./service-activated.component.scss']
})
export class ServiceActivatedComponent implements OnInit {
  originalServices: Service[]
  services: Service[]
  loading = true
  currentPage = 1
  totalItems: number
  q: string
  lastPage: number
  constructor(public service: ServicesService, private toastr: ToastrService,
              private modal: NgbModal
  ) {
  }
  ngOnInit() {
    this.init()
  }
  search() {
    if (!this.q) {this.services = this.originalServices}
    this.services = this.originalServices.filter(s => ['service_name', 'service_title']?.some(k => s[k]?.includes(this.q)))
  }
  init(resetPage= false) {
    if (resetPage) { this.currentPage = 1 }
    this.loading = true
    this.service.getActivatedServices().then(res => {
      this.originalServices = this.services = res.data
      this.loading = false
      console.log('services: ', this.services)
    }).catch(err => {
      console.log(err)
      this.toastr.error('something went wrong.')
    })
  }
  add() {
    const modal = this.modal.open(ServiceAddComponent)
    modal.result?.then((r) => {
      if (r === 'reload') {
        console.error('reloading...')
        this.init()
      }
    })
  }
}
