import {Injectable} from '@angular/core';
import {HttpClient, HttpContext, HttpHeaders} from "@angular/common/http";

@Injectable({
    providedIn: 'root'
})
export class HttpClientService {

    constructor(private http: HttpClient) {
    }

    replaceGetAscii(value): string {
        if (value === null || value === undefined) {
            return '';
        } else {
            return encodeURIComponent(value.toString());
        }
    }

    async postData(url: string, data: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        const headers = new HttpHeaders(Object.assign(header, {
            'Content-Type': 'application/json',
            "ngrok-skip-browser-warning":"0"
        }));
        const options = {headers};
        return  await this.http.post(
            url,
            data,
            options).toPromise();
    }
    async postFormData(url: string, data: any = {}, header: any = {}){
        return await this.http.post(url,data).toPromise();
    }

    async getData(url: string, dParams: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        let paramsUrl = '?';
        Object.entries(dParams).forEach(
            ([key, value], index) => {
                paramsUrl += index === 0 ? key + '=' + this.replaceGetAscii(value) : '&' + key + '=' + this.replaceGetAscii(value);
            }
        );
        return this.http.get(url + paramsUrl, {
            headers: Object.assign(header, {
                Accept: 'application/json',
                "ngrok-skip-browser-warning":"0"
            }),
            responseType: 'json',
        }).toPromise();
    }

    async putData(uri: string, dParams: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        const options = {
            headers: new HttpHeaders(Object.assign(header, {
                'Content-Type': 'application/json',
                "ngrok-skip-browser-warning":"0",
                Accept: 'application/json',
            })),
        };
        const result = this.http.put(uri, dParams, options).toPromise();
        return result.then((res: any) => {
            return res;
        });
    }

    async deleteData(uri: string, dParams: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        const options = {
            headers: new HttpHeaders(Object.assign(header, {
                'Content-Type': 'application/json',
                "ngrok-skip-browser-warning":"0",
                Accept: 'application/json',
            })),
        };
        const result = this.http.delete(uri, options).toPromise();
        return result.then((res: any) => {
            return res;
        });
    }

    async postFile(url: string, data: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        const headers = new HttpHeaders(Object.assign(header, {
            'undefined': 'multipart/form-data',
            "ngrok-skip-browser-warning":"0",
            'Accept': 'application/json'
        }));
        const options = {headers};
        return this.http.post(
            url,
            data,
            options).toPromise();
    }

    async getFile(url: string, data: any = {}, header: any = {}, isPrivate: boolean = true): Promise<any> {
        const headers = new HttpHeaders(Object.assign(header, {
            'Content-Type': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
            "ngrok-skip-browser-warning":"0",
            'Accept': 'application/json'
        }));
        const options = {headers};
        return this.http.post(
            url,
            data,
            options).toPromise();
    }
}
