import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {ServicesService} from '../../services.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {HttpErrorResponse} from '@angular/common/http';

@Component({
  selector: 'app-endpoints-add',
  templateUrl: './endpoints-add.component.html',
  styleUrls: ['./endpoints-add.component.scss']
})
export class EndpointsAddComponent implements OnInit {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private service: ServicesService
  ) {
  }

  @Input() serviceId: string
  @Output() reload = new EventEmitter()
  submitted = false
  keyTaken = false

  loading = false
  form = new FormGroup({
  "endpoint_route": new FormControl(null, [Validators.required]),
  "endpoint_payload_template":  new FormControl(null, [Validators.required]),
  "endpoint_payload_example":  new FormControl(null, [Validators.required]),
  "endpoint_response_example":  new FormControl(null, [Validators.required]),
  "endpoint_method": new FormControl(null),
  })
  ngOnInit() {
    console.error('serv id : ', this.serviceId)
  }
  save() {
    this.submitted = true
    if (!this.form) { return }
    const endpoint = this.form.value
    this.loading = true
    this.service.addEndpoint(this.serviceId, endpoint).then(res => {
      if (res?.success) {
        this.toastr.success('Endpoint created successfully ')
        this.activeModal.close('reload')
      } else { this.toastr.error('Something went wrong') }
    }).catch((err: HttpErrorResponse) => {
      this.toastr.error('Something went wrong')
    }).finally(() => this.loading = false)
    console.log('endpoint : ', endpoint)
  }
}
