import {Component, Input, OnInit} from '@angular/core';
import {Service} from '../../service';
import {Endpoint} from '../endpoint';
import {validate} from '../../../../utils/validation'
import {HttpClientService} from '../../../../../../@core/services/http-client.service';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {EndpointsAddComponent} from '../endpoints-add/endpoints-add.component';
import {ServicesService} from '../../services.service';
import {EndpointsEditComponent} from '../endpoints-edit/endpoints-edit.component';
import Swal from "sweetalert2";
import {ToastrService} from "ngx-toastr";

@Component({
  selector: 'app-endpoints-list',
  templateUrl: './endpoints-list.component.html',
  styleUrls: ['./endpoints-list.component.scss']
})
export class EndpointsListComponent implements OnInit {
  constructor(private http: HttpClientService,
              private modal: NgbModal,
              private toastr: ToastrService,
              private ser: ServicesService,
  ) {
  }
  @Input() service: Service
  @Input() subbed = false
  endpoint: Endpoint
  active = '0'
  loading = false
  loadingTest = false
  hasErrors: string
  copied = false
  submitted = false
  loadingEnd: number
  params = {}
  response: Response
  data

    protected readonly JSON = JSON;
  protected readonly Object = Object;
  ngOnInit() {
    this.init()
  }
  reload() {
    this.loading = true
    this.ser.byKey(this.service.service_key).then((res: Service) => {
      this.service.service_endpoints = res.service_endpoints
      this.active = '' + (this.service.service_endpoints.length - 1)
      this.setEndPoint()
      this.loading = false
    })
  }
  isBad(list) {
    // TODO REMOVE AFTER BACKEND FIXES ISSUE
    return JSON.stringify(list) === '[{}]'
  }
  init() {
    console.log('endpoints : ', this.service.service_endpoints)
    if (this.isBad(this.service.service_header_template)) {
      this.service.service_header_template = []
    }
    this.service.service_header_template = this.service.service_header_template.map(p => ({
      ...p,
      type: p.validation.includes('int') ? 'number' : 'text',
      minLength: RegExp(/minLength:(\d+)/).exec(p.validation)?.[1] ?? null,
      maxLength: RegExp(/maxLength:(\d+)/).exec(p.validation)?.[1] ?? null,
      required: p.validation.includes('required')
    }))
    this.service.service_endpoints = this.service.service_endpoints.map(e => {
      if (this.isBad(e.endpoint_payload_template)) {
        e.endpoint_payload_template = []
      }
      return{...e, endpoint_payload_template: e.endpoint_payload_template.map(p => ({
          ...p,
          type: p.validation.includes('int') ? 'number' : 'text',
          minLength: RegExp(/minLength:(\d+)/).exec(p.validation)?.[1] ?? null,
          maxLength: RegExp(/maxLength:(\d+)/).exec(p.validation)?.[1] ?? null,
          required: p.validation.includes('required')
        }))}
    })
    console.error('params finished : ', this.service.service_endpoints)
    this.setEndPoint()
  }

  addEndpoint() {
    let modal = this.modal.open(EndpointsAddComponent)
    modal.componentInstance.serviceId = this.service.service_id
    modal.result?.then((r) => {
      if (r === 'reload') {
        console.error('reloading end...')
        this.reload()
      }
    })
  }

  editEndpoint(endpoint: Endpoint) {
    let modal = this.modal.open(EndpointsEditComponent)
    modal.componentInstance.serviceKey = this.service.service_key
    modal.componentInstance.endpoint = endpoint
    modal.result?.then((r) => {
      if (r === 'reload') {
        console.error('reloading end...')
        this.reload()
      }
    })
  }

  copy() {
    const url = 'https://openapi.kifal.ma/service/' + this.service.service_key + this.endpoint.endpoint_route
    navigator.clipboard.writeText(url);
    this.copied = true
    setTimeout(() => {this.copied = false}, 2000)
  }


  test(e?: SubmitEvent) {
    this.submitted = true
    console.log(this.params)
    this.hasErrors = null
    for (const payload of this.endpoint.endpoint_payload_template) {
      if (!validate(payload.validation, this.params[payload.key])) {
        this.hasErrors = payload.key
        return;
      }
    }
    if (this.hasErrors) { return }
    console.log('valid : ', this.params)
    if (this.endpoint.endpoint_method === 'GET') {
      const urlParams = new URLSearchParams(this.params);
      const fullUrl = `${this.service.service_api_url + this.endpoint.endpoint_route}?${urlParams.toString()}`;
      this.loadingTest = true
      fetch(fullUrl, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      }).then(res => {
        this.loadingTest = false
        this.response = res
        if (res.ok) {return res.json()}
        console.log(res)
      }).then(data => {
        this.data = data
      })
    }
  }

  setEndPoint() {
    this.endpoint = this.service.service_endpoints[this.active]
    console.error('end :::: ', this.endpoint)
  }
  deleteEndpoint(endpoint: Endpoint) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#7367F0',
      cancelButtonColor: '#E42728',
      confirmButtonText: 'Yes, Delete Endpoint',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-danger ml-1'
      }
    }).then((result) => {
      if (result.value) {
        this.loadingEnd = endpoint.endpoint_id
        this.ser.deleteEndpoint(endpoint.endpoint_id).then(res => {
          this.toastr.success('Deleted Endpoint Successfully!')
          this.reload()
        }).catch(err => {
          this.toastr.error('Something Went Wrong')
        }).finally(() => {
          this.loadingEnd = null
        })
      }
    })
  }
}
