import {Injectable} from '@angular/core';
import {HttpClientService} from '../../../../@core/services/http-client.service';
import {Constants} from '../../../constants';

@Injectable({
    providedIn: 'root'
})
export class RolesService {

    constructor(
        private http: HttpClientService
    ) {
    }

    getRoleTemplate(): Promise<{ data: { roles_to_display: RoleTemplate } }> {
        return this.http.getData(Constants.API_CUSTOM_ROLES);
    }

    add(role: { role_name: string, role_permissions: string[] }) {
        return this.http.postData(Constants.API_CUSTOM_ROLES, {...role, role_group: role.role_name});
    }

    get() {
        return this.http.getData(Constants.API_ROLES_LIST);
    }

    getList() {
        return this.http.getData(Constants.API_ROLES_LIST_Basic);
    }

    update(id, role: Partial<Role>) {
        return this.http.putData(Constants.API_CUSTOM_ROLES + '/' + id, {
            ...role,
            role_group: role.role_name,
        });
    }

}

export interface RoleTemplate {
    [module: string]: { name: string, value: string }[];
}

export interface Role {
    role_id: number;
    customer_id: number;
    role_name: string;
    role_group_id: string;
    role_permissions: string[];
    user_count: number;
}

