import {Component, Input, OnInit} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {options, TokensService} from '../tokens.service';
import {Limits, Token} from '../token';
import {Service} from '../../services/service';
import {Constants} from '../../../../constants';
import {getLimited, limitLabels, unlimited} from '../../../utils/limits';

@Component({
    selector: 'app-token-view',
    templateUrl: './token-view.component.html',
    styleUrls: ['./token-view.component.scss']
})
export class TokenViewComponent implements OnInit {
    constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
                private tokens: TokensService,
    ) {
    }

    group = 'token';
    loadingSave = false;
    @Input() token: Token;
    services: Service[];
    loading = {details: true, services: true};
    limits: Limits;
    form = new FormGroup({
        'limit_daily': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_weekly': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_bi_weekly': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_monthly': new FormControl(null, [Validators.required, Validators.min(-1)])
    });
    labels = limitLabels;
    options = options;
    loadingUpdate = false;
    submitted = false;
    dform = new FormGroup({
        'expires_in': new FormControl(null),
        'token_name': new FormControl(null, [Validators.required]),
    });
    selectedLimit = false as any;

    ngOnInit() {
        this.dform.controls.token_name.setValue(this.token.token_name);
        this.tokens.byValue(this.token.token_value).then(res => {
            console.log('res token : ', res);
            this.limits = res.token.limits;
            this.selectedLimit = Object.keys(this.limits).find(k => this.limits[k] !== -1) || false;
            this.form.setValue(this.limits);
            this.loading.details = false;
        }).catch(err => {
            console.error(err);
            this.toastr.error(Constants.error, 'Can\'t get token');
        }).finally(() => {
            this.loading.details = false;
        });
        this.tokens.getServices(this.token.token_value).then(res => {
            this.services = res;
        }).catch(err => {
            this.toastr.error(Constants.error, 'Can\'t get services');
        }).finally(() => {
            this.loading.services = false;
        });
    }

    setLimit(key) {
        if (this.labels[key]) {
            let c: FormControl = this.form.controls[key];
            if (c.value === -1) {
                c.setValue(null);
            }
        }
    }

    update() {
        if (this.dform.invalid) {
            this.toastr.warning('Please fill the required fields');
            return;
        }
        this.loadingUpdate = true;
        this.tokens.update(this.token.token_value, this.dform.value).then(res => {
            this.toastr.success('Token updated successfully ');
            this.activeModal.close('reload');
        }).catch(err => {
            this.toastr.error(Constants.error);
        }).finally(() => {
            this.loadingUpdate = false;
        });
    }

    save() {
        let l = this.form.value[this.selectedLimit];
        this.form.setValue(unlimited);
        if (this.selectedLimit) {
            this.form.controls[this.selectedLimit].setValue(l);
        }
        console.log('form value : ', this.form.value);
        this.loadingSave = true;
        if (this.form.invalid) {
            this.toastr.warning('Please fill the required fields');
            return;
        }
        this.tokens.updateLimit(this.token.token_value, this.form.value as Limits).then(res => {
            this.toastr.success('Token limits updated successfully ');
            this.activeModal.dismiss();
        }).catch(err => {
            this.toastr.error(Constants.error);
        }).finally(() => {
            this.loadingSave = false;
        });
    }

}
