import {Injectable} from '@angular/core';
import {HttpClientService} from '../../../@core/services/http-client.service';
import {Constants} from '../../constants';
import {Limits} from "../../main/apps/tokens/token";

@Injectable({providedIn: 'root'})
export class UserService {
    constructor(private _http: HttpClientService) {
    }

    get() {
        return this._http.getData(Constants.API_USERS);
    }
    updateLimits(id:number,o:Partial<Limits>){
        return this._http.putData(Constants.API_USER_LIMITS+id,o);
    }
    add(o) {
        return this._http.postData(Constants.API_ADD_USER, o);
    }

    update(id: number, o) {
        return this._http.putData(Constants.API_ADD_USER + '/' + id, o);
    }
    byId(id:number){
        return this._http.getData(Constants.API_ADD_USER+'/'+id);
    }
    delete(id:number){
        return this._http.deleteData(Constants.API_ADD_USER+'/'+id);
    }

    current() {
        return this._http.getData(Constants.API_CURRENT_USER);
    }
}
