import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {TokensService} from '../tokens.service';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {TokensAddComponent} from '../tokens-add/tokens-add.component';
import {AuthenticationService} from '../../../../auth/service';
import {Token} from '../token';
import {ColumnMode, DatatableComponent} from '@swimlane/ngx-datatable';
import {ToastrService} from 'ngx-toastr';
import Swal from 'sweetalert2';
import {TokenViewComponent} from "../token-view/token-view.component";

@Component({
    selector: 'app-tokens-list',
    templateUrl: './tokens-list.component.html',
    styleUrls: ['./tokens-list.component.scss']
})
export class TokensListComponent implements OnInit {
    constructor(
        private token: TokensService,
        private toastr: ToastrService,
        protected auth: AuthenticationService,
        private modal: NgbModal
    ) {
    }

    @Input() showAdd = true;

    @ViewChild(DatatableComponent) table: DatatableComponent;
    @Output() activate = new EventEmitter();
    @Input() serviceKey: string;
    activating = false;
    toActivate: string;
    tokens: Token[];
    q: string;
    loading = false;
    unlinking: number;
    copied: number;
    originalTokens: Token[];

    protected readonly ColumnMode = ColumnMode;

    ngOnInit() {
        this.init();
    }

    unlink(token: Token) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You won\'t be able to revert this!',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#7367F0',
            cancelButtonColor: '#E42728',
            confirmButtonText: 'Yes, Unlink Token',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            }
        }).then((result) => {
            if (result.value) {
                this.unlinking = token.token_id;
                this.token.unlink(token.token_value, this.serviceKey).then(res => {
                    this.toastr.success('Unlinked the token successfully!');
                    // this.auth.removeService(this.serviceKey)
                    this.init();
                }).catch(err => {
                    this.toastr.error('something went wrong');
                }).finally(() => {
                    this.unlinking = null;
                });
            }
        });

    }

    copy(token: Token) {
        navigator.clipboard.writeText(token.token_value);
        this.copied = token.token_id;
        setTimeout(() => {
            this.copied = null;
        }, 2000);
    }

    init() {
        this.loading = true;
        this.token.get(this.serviceKey).then(res => {
            this.loading = false;
            this.originalTokens = this.tokens = res.data;
            console.log('res tokens : ', res);
        });
    }

    use(token: string) {
        this.activate.emit(token);
    }

    search() {
        const q = this.q;
        console.log('q: ', q);
        if (q) {
            this.tokens = this.originalTokens;
        }
        this.tokens = this.originalTokens.filter(t => t.token_name.includes(q));
        this.table.offset = 0;
    }

    add() {
        const modal = this.modal.open(TokensAddComponent, {
            size: 'lg'
        });
        modal.componentInstance.serviceKey = this.serviceKey;
        modal.result?.then((r) => {
            if (r === 'reload') {
                console.error('reloading end...');
                this.init();
            }
        });
    }
    view(token:Token){
        let modal=this.modal.open(TokenViewComponent,{
            size: 'lg'
        })
        modal.componentInstance.token=token
        modal.result?.then((r) => {
            if (r === 'reload') {
                console.error('reloading end...');
                this.init();
            }
        });
    }
}
