import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ServicesService} from '../services.service';

@Component({
  selector: 'app-limits-edit',
  templateUrl: './limits-edit.component.html',
  styleUrls: ['./limits-edit.component.scss']
})
export class LimitsEditComponent implements OnInit {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private service: ServicesService
  ) {
  }
  @Output() subbed = new EventEmitter()
  icons = {
    'customer': 'feather icon-users ',
    'user': 'feather icon-user ',
    'service': 'feather icon-command ',
    'token': 'feather icon-lock '
  }
  form = new FormGroup({
    "customer": new FormGroup({
      "limit_daily": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_weekly": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_bi_weekly": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_monthly": new FormControl(null, [Validators.required , Validators.min(-1)])
    }),
    "service":  new FormGroup({
      "limit_daily": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_weekly": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_bi_weekly": new FormControl(null, [Validators.required , Validators.min(-1)]),
      "limit_monthly": new FormControl(null, [Validators.required , Validators.min(-1)])
    }),
    "user": new FormGroup({
      "limit_daily": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_weekly": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_bi_weekly": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_monthly": new FormControl(-1, [Validators.required , Validators.min(-1)])
    }),
    "token": new FormGroup({
      "limit_daily": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_weekly": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_bi_weekly": new FormControl(-1, [Validators.required , Validators.min(-1)]),
      "limit_monthly": new FormControl(-1, [Validators.required , Validators.min(-1)])
    })
    // 'budget': new FormControl(null, [Validators.required , Validators.min(-1)]),
  })
  submitted = false
  loading = false
  ngOnInit() {
    this.form.disable()
    this.service.getLimits().then(res => {
      if (res?.data) {
        this.form.setValue(res?.data)
        this.form.enable()
      }
    })
  }

  save() {
    this.submitted = true
    this.loading = true
    if (this.form.invalid) { return }
    this.service.updateLimits(this.form.value).then(res => {
      this.loading = false
      console.log('res limits: ', res)
      this.toastr.success('Subscribed to Api successfully')
      this.activeModal.dismiss()
    })
  }
}
