import {Component, Input, OnInit} from '@angular/core';
import {Service} from '../../apps/services/service';
import {ServicesService} from '../../apps/services/services.service';

@Component({
    selector: 'app-service-card',
    templateUrl: './service-card.component.html',
    styleUrls: ['./service-card.component.scss']
})
export class ServiceCardComponent implements OnInit {
    @Input() service: Service;
    @Input() activated;
    subbed = false;

    constructor(private s: ServicesService) {
    }

    ngOnInit() {
        this.subbed = this.activated || this.s.isSubbed(this.service.service_key);
    }

}
