import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TokensListComponent } from './tokens-list/tokens-list.component';
import { TokensAddComponent } from './tokens-add/tokens-add.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {NgSelectModule} from '@ng-select/ng-select';
import {ComponentsModule} from '../../components/components.module';
import {NgxDatatableModule} from '@swimlane/ngx-datatable';
import {CoreDirectivesModule} from '../../../../@core/directives/directives';
import {RouterModule, Routes} from '@angular/router';
import { TokenViewComponent } from './token-view/token-view.component';
import {NgbNavModule} from "@ng-bootstrap/ng-bootstrap";
import {CorePipesModule} from "../../../../@core/pipes/pipes.module";


const appRoutes: Routes = [
    {
        component: TokensListComponent,
        path: 'all',
        pathMatch: 'full'
    },
];

@NgModule({
    declarations: [
        TokensListComponent,
        TokensAddComponent,
        TokenViewComponent,
    ],
    exports: [
        TokensListComponent
    ],
    imports: [
        RouterModule.forChild(appRoutes),
        CommonModule,
        ReactiveFormsModule,
        NgSelectModule,
        ComponentsModule,
        NgxDatatableModule,
        CoreDirectivesModule,
        FormsModule,
        NgbNavModule,
        CorePipesModule
    ]
})
export class TokensModule { }
