import {Injectable} from '@angular/core';
import {HttpErrorResponse} from '@angular/common/http';
import {BehaviorSubject, Observable} from 'rxjs';
import {User, Role} from 'app/auth/models';
import {ToastrService} from 'ngx-toastr';
import {Router} from '@angular/router';
import {SecureStorageService} from '../../../@core/services/secure-storage.service';
import {HttpClientService} from '../../../@core/services/http-client.service';
import {Constants} from '../../constants';

@Injectable({providedIn: 'root'})
export class AuthenticationService {
    public currentUser: Observable<User>;

    private currentUserSubject: BehaviorSubject<User>;

    constructor(private router: Router,
                private _http: HttpClientService,
                private _toastrService: ToastrService,
                private storage: SecureStorageService,
    ) {
        this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(this.storage.getItem('currentUser')));
        console.error('current user parsed : ', this.currentUserValue);
        this.currentUser = this.currentUserSubject.asObservable();
    }

    userCan(permission: string) {
        return this.currentUserValue.permissions.includes(permission);
    }

    appendService(key: string) {
        console.error('current : ', this.currentUserValue);
        let services = this.currentUserValue.customer_active_services;
        services.push(key);
        this.currentUserSubject.next({...this.currentUserValue, customer_active_services: services});
        this.storage.setItem('currentUser', JSON.stringify(this.currentUserValue));
    }

    removeService(key: string) {
        let user = this.currentUserValue;
        user.customer_active_services = user.customer_active_services.filter(s => s !== key);
        this.currentUserSubject.next(user);
        console.log(key, user);
        this.storage.setItem('currentUser', JSON.stringify(user));
    }

    // getter: currentUserValue
    public get currentUserValue(): User {
        return this.currentUserSubject.value;
    }

    /**
     *  Confirms if user is admin
     */
    get isAdmin() {
        return this.currentUser && this.currentUserSubject.value.role === Role.Admin;
    }

    /**
     *  Confirms if user is client
     */
    get isClient() {
        return this.currentUser && this.currentUserSubject.value.role === Role.Client;
    }

    async login(email: string, password: string) {
        await this._http.postData(Constants.API_LOGIN, {email, password}).then(res => {
            const user = res.User;
            console.log('user : ', user);
            if (!user) {
                throw new Error('no user');
            }
            const formattedUser: User = {
                id: user.id,
                email: user.email,
                roles: user.roles,
                permissions: res?.user_permissions,
                customer_active_services: user?.customer_active_services,
                token: res?.Token,
                firstName: user?.full_name,
                lastName: ''
            };
            this.currentUserSubject.next(formattedUser);
            this.storage.setItem('currentUser', JSON.stringify(formattedUser));
            this.storage.setItem('token', formattedUser.token ?? '');
            this.router.navigate(['/']);
            this._toastrService.success(' Welcome ' + formattedUser.firstName + '!');
        }).catch((err: HttpErrorResponse) => {
            if (err.status === 401) {
                if (err.error?.message) {
                    this._toastrService.error(' ' + err.error.message);
                } else {
                    this._toastrService.error(' Login or Password wrong.');
                }
            } else {
                console.log('err: ', err);
                this._toastrService.error(' Something went wrong, please try again later.');
            }
        });
    }

    updateProfile(user: Partial<User> = {}) {
        this.currentUserSubject.next({...this.currentUserValue, ...user});
        this.storage.setItem('currentUser', JSON.stringify(this.currentUserValue));
    }

    /**
     * User logout
     *
     */
    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        // notify
        this.currentUserSubject.next(null);
    }
}
