import {Component, OnInit} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {RolesService} from '../../roles/roles.service';
import {AuthenticationService, UserService} from '../../../../auth/service';
import {User1} from '../../../../auth/models';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
    selector: 'app-user-profile',
    templateUrl: './user-profile.component.html',
    styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {
    constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
                private role: RolesService,
                private auth: AuthenticationService,
                private user: UserService
    ) {
    }

    oldUser: User1;
    loadingCurr = true;

    selectedRoles: string[] = [];

    roles: { custom_role_id: number, custom_role_name: string }[];
    loading = false;
    form = new FormGroup({
        'first_name': new FormControl(null, [Validators.required, Validators.minLength(3)]),
        'last_name': new FormControl(null, [Validators.required, Validators.minLength(3)]),
        'email': new FormControl(null, [Validators.required, Validators.email]),
    });
    pform = new FormGroup({
        'password': new FormControl(null, [Validators.minLength(8)]),
        'passwordConfirmation': new FormControl(null, [Validators.minLength(8)])
    });
    taken = false;
    submitted = false;
    active: 'basic' | 'password';

    ngOnInit() {
        this.user.current().then(data => {
            this.oldUser = data;
            this.form.setValue({
                email: this.oldUser.email,
                first_name: this.oldUser.first_name,
                last_name: this.oldUser.last_name,
            });
            this.loadingCurr = false;
        }).catch(err => {
            this.toastr.error('Something went wrong.');
        });
        this.role.getList().then(res => {
            this.roles = res.data;
            let rolesbyName = {};
            this.roles.forEach(r => rolesbyName[r.custom_role_name] = r.custom_role_id);
            console.log('olduser : ', this.oldUser);
            this.selectedRoles = this.oldUser.roles.map(r => rolesbyName[r]);
            console.log(this.selectedRoles);
        });
    }

    changePassword() {
        this.submitted = true;
        if (this.pform.invalid) {
            return;
        }
        let o = this.pform.value;
        this.submit(o);
    }


    save() {
        if (this.active === 'basic') {
            this.submitted = true;
            this.taken = false;
            console.log('this.form.invalid', this.form);
            if (this.form.invalid) {
                return;
            }
            let o = {...this.form.value};
            this.loading = true;
            this.submit(o);
        } else {
            this.changePassword();
        }
    }

    submit(o) {

        this.user.update(this.oldUser.id, o).then(res => {
            if (res?.success) {
                this.auth.updateProfile(o);
                this.toastr.success('User Updated Successfully ');
                this.activeModal.dismiss();
                return;
            }
            this.toastr.error('Something went wrong.');
        }).catch(err => {
            console.log('err: ', err?.error?.error);
            if (err?.error?.error === 'unique-email') {
                this.toastr.error('Email already registered.');
                this.taken = true;
            } else {
                this.toastr.error('Something went wrong.');
            }
        }).finally(() => {
            this.loading = false;
        });
    }
}
