import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {NgbActiveModal} from "@ng-bootstrap/ng-bootstrap";
import {ToastrService} from "ngx-toastr";
import { ServicesService} from "../services.service";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {HttpErrorResponse} from "@angular/common/http";
import {Service} from '../service';

@Component({
  selector: 'app-service-edit',
  templateUrl: './service-edit.component.html',
  styleUrls: ['./service-edit.component.scss']
})
export class ServiceEditComponent implements OnInit {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private ser: ServicesService
  ) {
  }
  @Input() service: Service
  @Output() reload = new EventEmitter()
  @Output() keyChanged = new EventEmitter<string>()
  submitted = false
  keyTaken = false

  loading = false
  form = new FormGroup({
    'service_description': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_title': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_name': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_api_url': new FormControl(null, [Validators.required , Validators.minLength(5)]),
    'service_header_template': new FormControl(null ),
    'service_key': new FormControl(null, [Validators.required , Validators.minLength(5)]),
    'service_cost': new FormControl(null, [Validators.required ]),
  })
  ngOnInit() {
    console.error(this.service, this.form.value)
    const obj: any = {}
    for (const key in this.form.value) {
      obj[key] = this.service[key]
    }
    // temporary
    if (obj.service_header_template) { obj.service_header_template = JSON.stringify(obj.service_header_template) }
    this.form.setValue(obj)
  }
  save() {
    this.keyTaken = false
    this.submitted = true
    if (!this.form) { return }
    const service = {...this.form.value, service_id: this.service.service_id}
    this.loading = true
    this.ser.update(service, this.service.service_key).then(res => {
      if (res?.success) {
        let nkey=service.service_key
        if(nkey!==this.service.service_key){
          this.keyChanged.emit(nkey)
        }
        this.toastr.success('Service updated successfully ')
        this.activeModal.close('reload')
      } else { this.toastr.error('Something went wrong') }
    }).catch((err: HttpErrorResponse) => {
      console.error('')
      if (err.error?.errors?.service_key) {
        this.keyTaken = service.service_key
      } else { this.toastr.error('Something went wrong') }
    }).finally(() => this.loading = false)
    console.log('service : ', service)
  }
}
