import {Component, EventEmitter, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-service-subscribe-modal',
  templateUrl: './service-subscribe-modal.component.html',
  styleUrls: ['./service-subscribe-modal.component.scss']
})
export class ServiceSubscribeModalComponent {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService) {
  }
  @Output() subbed = new EventEmitter()
  form = new FormGroup({
    'credit': new FormControl(null, Validators.required),
    'budget': new FormControl(null, [Validators.required , Validators.min(100)]),
  })
  submitted = false
  sub() {
    this.submitted = true
    if (this.form.invalid) { return }
    this.toastr.success('Subscribed to Api successfully')
    this.subbed.emit(this.form.value)
    this.activeModal.dismiss()
  }

}
