import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {ServicesService} from "../../apps/services/services.service";
import {Service} from "../../apps/services/service";
import {ToastrService} from "ngx-toastr";

@Component({
  selector: 'app-find-service',
  templateUrl: './find-service.component.html',
  styleUrls: ['./find-service.component.scss']
})
export class FindServiceComponent implements  OnInit{
  services:Service[]
  originalServices:Service[]
  selectedKey:string
  @Output() selected=new EventEmitter()
  loading=true
  active=false
  q:string
  constructor(private service:ServicesService,
              private toastr: ToastrService
              ) {
  }
  ngOnInit() {
    this.init()
  }
  select(s:Service){
    this.selectedKey=s.service_key
    console.log('selected : ',this.selectedKey)
    this.selected.emit(s)
  }
  search() {
    console.log(this.q)
    if (!this.q) {this.services = this.originalServices}
    this.services = this.originalServices.filter(s => ['service_name', 'service_title']?.some(k => s[k]?.includes(this.q)))
  }

  init(){
    this.loading=true
    this.service.getActivatedServices().then(res=>{
      this.originalServices=this.services=res?.data
      console.log('done',this.loading)
    }).catch(err=>{
      this.toastr.error('Something went wrong.')
    }).finally(()=>{
      this.loading=false
    })
  }
}
