import {Limits} from '../apps/tokens/token';

export const limitLabels = {
    'limit_daily': 'Daily Limit',
    'limit_weekly': 'Weekly Limit',
    'limit_bi_weekly': 'Biweekly Limit',
    'limit_monthly': 'Monthly Limit'
};
export const unlimited = {
    'limit_daily': -1,
    'limit_weekly': -1,
    'limit_bi_weekly': -1,
    'limit_monthly': -1,
};

export function getLimited(limits: Limits) {
    console.log('limits', limits);
    let l = {};
    for (let k in limits) {
        l[k] = limits[k] !== -1;
    }
    return l;
}
