import {Injectable} from '@angular/core';
import {HttpClientService} from '../../../../@core/services/http-client.service';
import {Constants} from '../../../constants';
import { Limits} from "./token";

@Injectable({
    providedIn: 'root'
})
export class TokensService {

    constructor(
        private http: HttpClientService,
    ) {
    }

    get(key?: string) {
        if (key) {
            return this.http.getData(Constants.API_SERVICE_TOKENS + key)
        } else {
            return this.http.getData(Constants.API_CUSTOMER_TOKENS)
        }
    }
    byValue(token:string){
        return this.http.getData(Constants.API_TOKENS + '/' + token)
    }

    unlink(token: string, service_key: string) {
        return this.http.postData(Constants.API_UNLINK_TOKEN, {token, service_key})
    }
    updateLimit(token:string,limits:Limits){
        return this.http.putData(Constants.API_TOKENS + '/limit/' + token,limits)
    }
    update(token:string,o){
        return this.http.putData(Constants.API_TOKENS + '/' + token,o)
    }

    getServices(tokenValue) {
        return this.http.getData(Constants.API_TOKENS + '/' + tokenValue + '/services')
    }

    createServiceToken(o: { token_name?: string, expires_in?: string, service_key: string }) {
        if (!o.service_key) {
            return this.add(o)
        }
        return this.http.postData(Constants.API_TOKEN_SERVICE, o)
    }

    add(token) {
        return this.http.postData(Constants.API_TOKENS, token)
    }
}
export const options = [{label: 'Never', value: 'N'},{label: 'Week', value: 'W'}, {label: 'Month', value: 'M'}, {
    label: 'Day',
    value: 'D'
}, {label: 'Hour', value: 'H'}, {label: 'Year', value: 'Y'}]
