import {Component, EventEmitter, Output} from '@angular/core';
import {NgbActiveModal} from "@ng-bootstrap/ng-bootstrap";
import {ToastrService} from "ngx-toastr";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import { ServicesService} from "../services.service";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
  selector: 'app-service-add',
  templateUrl: './service-add.component.html',
  styleUrls: ['./service-add.component.scss']
})
export class ServiceAddComponent {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private service: ServicesService
  ) {
  }

  @Output() reload = new EventEmitter()
  submitted = false
  keyTaken = false

  loading = false
  form = new FormGroup({
    'service_name': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_description': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_title': new FormControl(null, [Validators.required , Validators.minLength(3)]),
    'service_endpoint': new FormControl(null, [Validators.required , Validators.minLength(2)]),
    'service_endpoint_method': new FormControl(null, [Validators.required]),
    'service_api_url': new FormControl(null, [Validators.required , Validators.minLength(5)]),
    'service_header_template': new FormControl(null, ),
    'service_key': new FormControl(null, [Validators.required , Validators.minLength(5)]),
    'service_cost': new FormControl(null, [Validators.required ]),
  })
  save() {
    this.keyTaken = false
    this.submitted = true
    if (!this.form) { return }
    const service = this.form.value
    this.loading = true
    this.service.add(service).then(res => {
      if (res?.success) {
        this.toastr.success('Service created successfully ')
        this.activeModal.close('reload')
      } else { this.toastr.error('Something went wrong') }
    }).catch((err: HttpErrorResponse) => {
      if (err.error?.errors?.service_key) {
        this.keyTaken = service.service_key
      } else { this.toastr.error('Something went wrong') }
    }).finally(() => this.loading = false)
    console.log('service : ', service)
  }
}
