const validators = {
    required: function(value) {
        if (value === undefined || value === null) { return false; }
        if (typeof value === 'string' || Array.isArray(value)) { return value.length > 0; }
        return true;
    },
    string: function(value) {
        return typeof value === 'string';
    },
    int: function(value) {
        return Number.isInteger(value);
    },
    email: function(value) {
        const emailRegex = /^[A-Za-z0-9]+([._%+-]?[A-Za-z0-9]+)*@[A-Za-z0-9-]+\.[A-Za-z]{2,}$/;
        return typeof value === 'string' && emailRegex.test(value);
    },
    min_length: function(value, size) {
        return typeof value === 'string' && value.length >= size;
    },
    max_length: function(value, size) {
        return typeof value === 'string' && value.length <= size;
    }
};

export function validate(validation, value) {
    const validationParts = validation.split("|");

    for (const part of validationParts) {
        let isValid;
        if (part.includes(":")) {
            const [validatorName, arg] = part.split(":");
            const validator = validators[validatorName];
            isValid = validator ? validator(value, parseInt(arg, 10)) : true;
        } else {
            const validator = validators[part];
            isValid = validator ? validator(value) : true;
        }

        if (!isValid) {
            return false;
        }
    }

    return true;
}

export function tryParse(t: string) {
    try {
        return JSON.parse(t)
    } catch (err) {
        return t
    }
}
