import {Component, EventEmitter, Input, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {HttpErrorResponse} from '@angular/common/http';
import {options, TokensService} from '../tokens.service';
import {Service} from "../../services/service";

@Component({
  selector: 'app-tokens-add',
  templateUrl: './tokens-add.component.html',
  styleUrls: ['./tokens-add.component.scss']
})
export class TokensAddComponent {
  constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
              private token: TokensService
  ) {
  }

  @Input() serviceKey: string
  @Output() reload = new EventEmitter()
  options = options
  submitted = false
  keyTaken = false
  selected:Service

  loading = false
  form = new FormGroup({
    "expires_in": new FormControl("M", [Validators.required]),
    "token_name":  new FormControl(null, [Validators.required]),
  })
  setService(s:Service){
    this.serviceKey=s.service_key
  }
  save() {
    this.submitted = true
    if (!this.form) { return }
    const token = this.form.value
    this.loading = true
    this.token.createServiceToken({...token,service_key:this.serviceKey}).then(res => {
      console.log(res,res?.success)
      if (res?.success) {
        this.toastr.success('Token created successfully ')
        this.activeModal.close('reload')
      } else { this.toastr.error('Something went wrong') }
    }).catch((err: HttpErrorResponse) => {
      this.toastr.error('Something went wrong')
    }).finally(() => this.loading = false)
    console.log('token : ', token)
  }
}
