import {Component, OnInit} from '@angular/core';
import { ServicesService} from "../services.service";
import {ToastrService} from "ngx-toastr";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {ServiceAddComponent} from "../service-add/service-add.component";
import {Service} from '../service';

@Component({
  selector: 'app-services-list',
  templateUrl: './services-list.component.html',
  styleUrls: ['./services-list.component.scss']
})
export class ServicesListComponent implements OnInit {
  services: Service[]
  loading = true
  currentPage = 1
  totalItems: number
  q: string
  lastPage: number
  constructor(public service: ServicesService, private toastr: ToastrService,
              private modal: NgbModal
  ) {
  }
  ngOnInit() {
    this.init()
  }
  init(resetPage= false) {
    if (resetPage) { this.currentPage = 1 }
    this.loading = true
    this.service.getServices(this.currentPage, this.q).then(res => {
      this.services = res.data
      this.totalItems = res.total
      this.lastPage = res.lastPage
      this.loading = false
      console.log('services: ', this.services)
    }).catch(err => {
      this.toastr.error('something went wrong.')
    })
  }
  add() {
    const modal = this.modal.open(ServiceAddComponent)
    modal.result?.then((r) => {
      if (r === 'reload') {
        console.error('reloading...')
        this.init()
      }
    })
  }

}
