import {Component, OnInit} from '@angular/core';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import { ServicesService} from "../services.service";
import {ActivatedRoute} from "@angular/router";
import {Service} from '../service';
import {ToastrService} from 'ngx-toastr';
import {AuthenticationService} from '../../../../auth/service';
import {Token} from "../../tokens/token";

@Component({
  selector: 'app-service-full',
  templateUrl: './service-full.component.html',
  styleUrls: ['./service-full.component.scss']
})
export class ServiceFullComponent implements OnInit {
  constructor(private modalService: NgbModal,
              private ser: ServicesService,
              private auth: AuthenticationService,
              private toastr: ToastrService,
              private route: ActivatedRoute
  ) {
  }
  loading = true
  loadingActivate = false
  service: Service

  related = [{}, {}, {}, {}]
  info = {
    'credit': 300000,
    'apikey': '7fxMNrbZCRc4VN5J68MVyH4RrcLumjko',
    'used': 15000,
    'budget': 32233
  }
  subbed = false
  activating = false
  breadcrumb
  ngOnInit() {
    this.init()
  }
  init() {
    const key = this.route.snapshot.paramMap.get('key')
    this.loading = true
    this.ser.byKey(key).then(res => {
      this.service = res
      this.breadcrumb = {
        links: [
          {
            name: 'API Services list',
            isLink: true,
            link: '/services/list'
          },
          {
            name: this.service.service_name,
            isLink: false,
          }
        ]
      }
      console.log('this.auth.currentUserValue.customer_active_services: ', this.auth.currentUserValue.customer_active_services)
      this.subbed = this.auth.currentUserValue.customer_active_services?.includes(key)
      this.loading = false
    })
  }
  cancel() {
    this.subbed = false
  }
  activate(token: string) {
    this.sub(token)
  }

  sub(token?: string) {
    this.loadingActivate = true
    this.ser.activate(this.service.service_key, token).then(res => {
      let token=res.data as Token
      this.service.service_main_token=token.token_value
      this.loadingActivate = false
      this.auth.appendService(this.service.service_key)
      this.subbed = true
      this.toastr.success( 'Activated Service Successfully ')
      console.log('activated res: ', res)
    })
  }

  openSub() {
    this.sub()
    // TODO add modal before activation
    // const modal = this.modalService.open(ServiceSubscribeModalComponent, {
    //         windowClass: 'modal fade',
    //         modalDialogClass: 'modal-slide-in sidebar-todo-modal'
    //     })
    // modal.componentInstance.subbed.subscribe(v => {
    //   this.info = {...v, used: 0, token: '7fxMNrbZCRc4VN5J68MVyH4RrcLumjko'}
    //   this.subbed = true
    // })

  }
}
